/**
 * Tip Component for tinglejs
 */

var classnames = require('classnames');
var Layer = require('tingle-layer');

class Tip extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            show: false
        }
    }

    show(options) {
        var width = options.icon ? options.width || this.props.width : 216;
        this.setState({
            width: width,
            text: options.text,
            icon: options.icon,
            onHide: options.onHide || this.props.onHide,
            show: true,
            mask: 'mask' in options ? options.mask : true,
            duration: options.duration
        });
    }

    hide() {
        clearTimeout(this.timer);
        this.setState({
            show: false
        });
        this.props.onHide();
    }

    render() {
        var t = this,
            {icon, text, show, duration, ...other} = this.state,
            classSet = {
                'tF14 tFf tFAC tLH1_5': true,
                'tPL20 tPR20': !icon
            };

        icon = icon ? <div className="tFAC tPB14 tPR">
                <i className={"tIcon tIcon" + (icon.charAt(0).toUpperCase() + icon.substring(1))}></i>
            </div> : '';

        this.timer && clearTimeout(this.timer);
        this.timer = show && setTimeout( function () {
            t.hide();
        }, duration || 3000);

        return (
            <Layer show={show}  {...other}>
                <div className="tTip tPT20 tPB20 tR6">
                    {icon}
                    <div className={classnames(classSet)}>{text}</div>
                </div>
            </Layer>
        );
    }
}

Tip.defaultProps = {
    onHide: function () {},
    width: 136
}

// http://facebook.github.io/react/docs/reusable-components.html
Tip.propTypes = {
}


var WRAPPER_ID = '__TingleGlobalTip__';
var doc = document;

Tip.global = null;
Tip.show = function (options) {
    // 
    if (!Tip.global) {
        var wrapper = doc.getElementById(WRAPPER_ID);
        if (!wrapper) {
            wrapper = doc.createElement('div');
            wrapper.id = WRAPPER_ID;
            doc.body.appendChild(wrapper);
        }
        Tip.global = React.render(<Tip/>, wrapper);
    }
    Tip.global.show(options);
}


module.exports = Tip;


