/**
 * Dialog Component for tinglejs
 */

var classnames = require('classnames');
var Layer = require('tingle-layer');

class Dialog extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            show: this.props.show
        }
    }

    _show(options) {
        this.show(options, true);
    }

    show(options) {
        var state = this.state,
            prop = options ? options : this.props;

        state.show = true;
        state.title = prop.title;
        state.children = prop.children;
        state.buttons = prop.buttons;

        this.setState(state);
    }

    hide() {
        this.state.show = false;
        this.setState(this.state);
    }

    handleClick(callback) {
        this.hide();
        callback && callback();
    }

    render() {
        var t = this,
            btn = '',
            {className, show, ...other} = this.props,
            title = this.state.title,
            buttons = this.state.buttons,
            classSet = {
                'tDialog tR5 tPT20': true,
                [this.props.className]: !!this.props.className
            };

        btn = buttons && buttons.map( function (item) {
            var callback = item.callback || function () {};
            return <div className={"tFB1" + (item.primary ?  " tPrimary" : "")} onClick={t.handleClick.bind(t, item.callback)}>
                    {item.text}
                </div>
        });


        return (
            <Layer show={this.state.show} {...other}>
                <div className={classnames(classSet)}>
                    {title ? <h1 className="tFAC tFS16 tFC3">{title}</h1> : ''}

                    <div className="tPL16 tPR16 tPT8 tPB20 tFS14 tFC3 tFAC tLH1_5">
                        {this.state.children}
                    </div> 

                    <div className="tOperation tFBH tFAC tH44 tF16 tLH44">
                        {btn}
                    </div>
                </div>
            </Layer>
        );
    }
}

Dialog.defaultProps = {
    buttons: [{
        text: ' ',
        callback: function () {},
        primary: true
    }],
    mask: true,
    show: false
}

// http://facebook.github.io/react/docs/reusable-components.html
Dialog.propTypes = {
}


var WRAPPER_ID = '__TingleGlobalDialog__';
var doc = document;

Dialog.global = null;
function show (options) {
    // 
    if (!Dialog.global) {
        var wrapper = doc.getElementById(WRAPPER_ID);
        var {...other} = options;
        
        if (!wrapper) {
            wrapper = doc.createElement('div');
            wrapper.id = WRAPPER_ID;
            doc.body.appendChild(wrapper);
        }

        Dialog.global = React.render(<Dialog {...other}  />, wrapper);
    }
    Dialog.global._show(options);
}
Dialog.alert = function (options) {
    options.buttons = [{
        text: options.confirmText || ' ',
        callback: options.onConfirm,
        primary: true
    }];
    show(options);
}
Dialog.confirm = function (options) {
    options.buttons = [{
        text: options.cancelText || ' ',
        callback: options.onCancel
    },{
        text: options.confirmText || ' ',
        callback: options.onConfirm,
        primary: true
    }];
    show(options);
}

module.exports = Dialog;


