/**
 * Layer Component for tinglejs
 */

var Mask = require('tingle-mask');


class Layer extends React.Component {

    componentWillMount() {
        document.documentElement.style.height = '100%';
        document.body.style.height = '100%';
        this.handleMask(this.props);
    }

    constructor(props) {
        super(props);
    }

    componentWillUpdate(props, state) {
        this.handleMask(props);
    }

    handleMask(props) {
        if (props.mask) {
            props.show ? Mask.show({
                opacity: 0.2
            }) : Mask.hide();
        }
    }

    getStyle() {
        var prop = this.props,
            show = prop.show,
            width = 'width' in prop,
            height = 'height' in prop,
            full = prop.mode == 'full',
            top = 'top' in prop,
            bottom = 'bottom' in prop,
            left = 'left' in prop,
            right = 'right' in prop,
            style = {
                width: full || !width ? '100%' :  prop.width,
                height: full ? '100%' : height ? prop.height : 'auto'
            };

        if (full) {
            style.top = 0;
            style.left = 0;
        } else {
            if (top) {
               style.top = prop.top;
            } else if (bottom) {
               style.bottom = prop.bottom;
            } else {
               style.top = '50%';
               style['WebkitTransform'] = (style['WebkitTransform'] || '' ) + ' translateY(-50%)';
               style['transform'] = (style['transform'] || '') + ' translateY(-50%)';
            }

            if (left) {
               style.left = prop.left;
            } else if (right) {
               style.right = prop.right;
            } else {
               style.left = '50%';
               style['WebkitTransform'] = (style['WebkitTransform'] || '') + ' translateX(-50%)';
               style['transform'] = (style['transform'] || '') +  ' translateX(-50%)';
            } 
        }

        style.zIndex = prop.zIndex;
        style.display = show ? 'block' : 'none';

        return style;
    }

    render() {
        return (
            <div className="tLayer" style={this.getStyle()}>{this.props.children}</div>
        );
    }
}

Layer.defaultProps = {
    zIndex: 1001,
    mask: false,
    show: false 
}

// http://facebook.github.io/react/docs/reusable-components.html
Layer.propTypes = {
}




module.exports = Layer;


