/**
 * OnOff Component for tinglejs
 */
class OnOff extends React.Component {

    constructor(props) {
        super(props);
        // this.state = {
        //     on:t.this.props.on
        // }
    }
    /*
    * options
    */
    handleChange() {
        var t =this;
       // t.state.on=!t.state.on;
       // t.setState(t.state);
        t.props.onChange(!t.props.on)
    }

    render() {
    	var t= this;
        var cx = React.addons.classSet;
        var classSet ={
            "tOnOffPannel":true,
            "tOn":t.props.on,
            [t.props.className]: !!t.props.className
        };
        return (
        	<div className={cx(classSet)} onClick={t.handleChange.bind(this)}><div className="tOnOffRadius"></div></div>
        );
    }
}

OnOff.defaultProps = {
    on:true,
	onChange:function(){}
}

// http://facebook.github.io/react/docs/reusable-components.html
OnOff.propTypes = {
    on:React.PropTypes.bool,
    onChange:React.PropTypes.func
}

module.exports = OnOff;



